button {
  padding: 10px 20px;
  background-color: #0052cc;
  color: white;
  border: none;
  cursor: pointer;
  margin: 10px;
  border-radius: 5px;
  font-size: 16px;
  transition: all 0.3s ease;
}
button:hover {
  background-color: #3e8e41;
}
button:active {
  transform: scale(0.95);
}
button:focus {
  outline: none;
}
.counter-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 250px;
}

.para {
  font-size: 28px;
}
